import React, { useState, useEffect } from 'react';
import { BarChart, Bar, ResponsiveContainer, XAxis, YAxis, CartesianGrid, Rectangle } from 'recharts';
import "../chart.css"
const SalesConversion = () => {
    const [data, setData] = useState([
        { name: 'Jan', uv: 4000, pv: 2400, amt: 400 },
        { name: 'Feb', uv: 3000, pv: 1398, amt: 210 },
        { name: 'Mar', uv: 2000, pv: 9800, amt: 290 },
        { name: 'Apr', uv: 2780, pv: 3908, amt: 100 },
        { name: 'May', uv: 1890, pv: 4800, amt: 181 },
        { name: 'Jun', uv: 2390, pv: 3800, amt: 500 },
        { name: 'July', uv: 3490, pv: 4300, amt: 100 },
        { name: 'Aug', uv: 3490, pv: 4300, amt: 330 },
        { name: 'Sep', uv: 3490, pv: 4300, amt: 430 },
        { name: 'Oct', uv: 3490, pv: 4300, amt: 140 },
        { name: 'Nov', uv: 3490, pv: 4300, amt: 230 },
        { name: 'Dec', uv: 3490, pv: 4300, amt: 110 },
    ]);

    // useEffect hook example (optional, use it if needed)
    useEffect(() => {
        setData([
            { name: 'Jan', uv: 4000, pv: 2400, amt: 400 },
            { name: 'Feb', uv: 3000, pv: 1398, amt: 210 },
            { name: 'Mar', uv: 2000, pv: 9800, amt: 290 },
            { name: 'Apr', uv: 2780, pv: 3908, amt: 100 },
            { name: 'May', uv: 1890, pv: 4800, amt: 181 },
            { name: 'Jun', uv: 2390, pv: 3800, amt: 500 },
            { name: 'July', uv: 3490, pv: 4300, amt: 100 },
            { name: 'Aug', uv: 3490, pv: 4300, amt: 330 },
            { name: 'Sep', uv: 3490, pv: 4300, amt: 430 },
            { name: 'Oct', uv: 3490, pv: 4300, amt: 140 },
            { name: 'Nov', uv: 3490, pv: 4300, amt: 230 },
            { name: 'Dec', uv: 3490, pv: 4300, amt: 110 },
        ])
    }, []);

    const [activeTab, setActiveTab] = useState('totalsales');

    const handleTabClick = (tab) => {
        setActiveTab(tab);
    };
    return (
        <>
            <div >
                <ul className="nav nav-tabs">
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'totalsales' ? 'active' : ''}`} onClick={() => handleTabClick('totalsales')}>
                            <p>Total sales</p>
                            <h3>$84.8k</h3>
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'videosales' ? 'active' : ''}`} onClick={() => handleTabClick('videosales')}>
                            <p>Sales from videos</p>
                            <h3>$42.4k</h3>
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'productsales' ? 'active' : ''}`} onClick={() => handleTabClick('productsales')}>
                            <p>Sales from products</p>
                            <h3>$42.4k</h3>
                        </div>
                    </li>

                </ul>
                {/* <div className="tab-content mt-3">
                    <div className={`tab-pane fade ${activeTab === 'totalsales' ? 'show active' : ''}`} id="totalsales" role="tabpanel">
                        <h5>Content of Tab 1</h5>
                    </div>
                    <div className={`tab-pane fade ${activeTab === 'videosales' ? 'show active' : ''}`} id="videosales" role="tabpanel">
                        <h5>Content of Tab 2</h5>
                    </div>
                    <div className={`tab-pane fade ${activeTab === 'productsales' ? 'show active' : ''}`} id="productsales" role="tabpanel">
                        <h5>Content of Tab 3</h5>
                    </div>
                </div> */}
            </div>
            <ResponsiveContainer className="chart_engagement" width="100%" height="100%">
                <BarChart
                    width={500}
                    height={300}
                    data={data}
                    margin={{
                        top: 20,
                        right: 0,
                        left: 0,
                        bottom: 5,
                    }}
                >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" />
                    <YAxis />
                    {/* <Tooltip /> */}
                    {/* <Legend /> */}
                    <Bar dataKey="amt" maxBarSize={20} fill="#77BBFF" activeBar={<Rectangle fill="pink" stroke="blue" />} />
                    {/* <Bar dataKey="uv" fill="#82ca9d" activeBar={<Rectangle fill="gold" stroke="purple" />} /> */}
                </BarChart>
            </ResponsiveContainer>
        </>
    );
};

export default SalesConversion;
