import { useState, useEffect } from 'react';

function formatNumber(number) {
    if (number >= 1e9) {
        return (number / 1e9).toFixed(1) + 'b';
    }
    if (number >= 1e6) {
        return (number / 1e6).toFixed(1) + 'm';
    }
    if (number >= 1e3) {
        return (number / 1e3).toFixed(1) + 'k';
    }
    if (Number.isInteger(number)) {
        return number;
    } else {
        return parseFloat(number).toFixed(2);
    }
}

function ZeroToHeroCount({ targetNumber }) {
    const [currentNumber, setCurrentNumber] = useState(0);

    useEffect(() => {
        const duration = 2000; // Animation duration in milliseconds
        const increment = Math.ceil(targetNumber / (duration / 50)); // Assuming 50 steps per second

        const interval = setInterval(() => {
            setCurrentNumber((prevNumber) => {
                const newNumber = prevNumber + increment;
                return newNumber >= targetNumber ? targetNumber : newNumber;
            });
        }, 20); // Update every 20 milliseconds

        return () => clearInterval(interval); // Cleanup function to clear interval on unmount
    }, [targetNumber]);

    return formatNumber(currentNumber);
}

export default ZeroToHeroCount;
