function numberFormatShort(n, precision = 1) {
    if (n < 900) {
        // 0 - 900
        let n_format = parseInt(n);
        let suffix = "";
        return n_format + suffix;
    } else if (n < 900000) {
        // 0.9k-850k
        let n_format = (parseInt(n) / 1000).toFixed(precision);
        let suffix = "K";
        return n_format + suffix;
    } else if (n < 900000000) {
        // 0.9m-850m
        let n_format = (parseInt(n) / 1000000).toFixed(precision);
        let suffix = "M";
        return n_format + suffix;
    } else if (n < 900000000000) {
        // 0.9b-850b
        let n_format = (parseInt(n) / 1000000000).toFixed(precision);
        let suffix = "B";
        return n_format + suffix;
    } else if (n === undefined || n === null) {
        return 0
    } else {
        // 0.9t+
        let n_format = (parseInt(n) / 1000000000000).toFixed(precision);
        let suffix = "T";
        return n_format + suffix;
    }
}

function animateCount(targetNumber, duration) {
    let currentNumber = 0;
    const increment = Math.ceil(targetNumber / (duration / 50)); // Assuming 50 steps per second

    const interval = setInterval(() => {
        currentNumber += increment;
        document.getElementById('counter').innerText = currentNumber;

        if (currentNumber >= targetNumber) {
            clearInterval(interval);
            document.getElementById('counter').innerText = targetNumber;
        }
    }, 20); // Update every 20 milliseconds
}
module.exports = {
    numberFormatShort,
    animateCount
}