import React, { useState, useEffect, useCallback, useLayoutEffect } from 'react';
import { BarChart, Bar, ResponsiveContainer, XAxis, YAxis, CartesianGrid, Rectangle, Label, Tooltip, Legend } from 'recharts';
import "../chart.css"
import ZeroToHeroCount from '../ZeroToHeroCount';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import axios from 'axios';
import { format } from 'date-fns';
import { MdOutlineKeyboardArrowRight } from "react-icons/md";
import { FallingLines, InfinitySpin, TailSpin } from 'react-loader-spinner';

const EngageMent = ({ selectedDate, setTab, setAllAnalyticsData, setLoading }) => {
    const [data, setData] = useState([])
    const [overalldata, setOverallData] = useState({})
    // const [loading, setLoading] = useState(true)

    const user = useSelector((state) => state.auth.user)
    function convertSecondsToTime(seconds) {
        if (isNaN(seconds) || seconds < 0) {
            return "0";
        }

        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const remainingSeconds = seconds % 60;

        if (hours > 0) {
            return `${hours}hr ${minutes}min ${remainingSeconds}sec`;
        } else if (minutes > 0) {
            return `${minutes}min ${remainingSeconds}sec`;
        } else {
            return `${remainingSeconds}sec`;
        }
    }
    // useEffect hook example (optional, use it if needed)
    // useEffect(() => {
    //     setData([
    //         { name: 'Jan', uv: 4000, pv: 2400, amt: 400 },
    //         { name: 'Feb', uv: 3000, pv: 1398, amt: 210 },
    //         { name: 'Mar', uv: 2000, pv: 9800, amt: 290 },
    //         { name: 'Apr', uv: 2780, pv: 3908, amt: 100 },
    //         { name: 'May', uv: 1890, pv: 4800, amt: 181 },
    //         { name: 'Jun', uv: 2390, pv: 3800, amt: 500 },
    //         { name: 'July', uv: 3490, pv: 4300, amt: 100 },
    //         { name: 'Aug', uv: 3490, pv: 4300, amt: 330 },
    //         { name: 'Sep', uv: 3490, pv: 4300, amt: 430 },
    //         { name: 'Oct', uv: 3490, pv: 4300, amt: 140 },
    //         { name: 'Nov', uv: 3490, pv: 4300, amt: 230 },
    //         { name: 'Dec', uv: 3490, pv: 4300, amt: 110 },
    //     ])
    // }, []);

    const [activeTab, setActiveTab] = useState('tab1');

    const handleTabClick = (tab) => {
        setActiveTab(tab);
    };
    // {
    //     "buyNow": 1,
    //         "addtoCart": 0,
    //             "askQuestion": 0,
    //                 "date": "2024_04_02",
    //                     "unique_views": 33,
    //                         "id": "236633565_2024_04_02",
    //                             "brand_id": "236633565",
    //                                 "total_views": 35,
    //                                     "watch_time": 815,
    //                                         "engagement_rate": "8.57",
    //                                             "ctr_rate": "94.29%"
    // }
    function calculateTotals(data) {
        return data.reduce((totals, entry) => {
            totals.unique_views += parseInt(entry.total_unique_views);
            totals.total_views += parseInt(entry.total_views);
            totals.watch_time += parseInt(entry.total_watchtime);
            totals.buyNow += parseInt(entry.buyNow);
            totals.addtoCart += parseInt(entry.addtoCart);
            totals.askQuestion += parseInt(entry.askQuestion);

            return totals;
        }, { buyNow: 0, addtoCart: 0, askQuestion: 0, unique_views: 0, total_views: 0, watch_time: 0 });
    }
    function addBarDate(array) {
        const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

        return array.map(obj => {
            const [year, month, day] = obj.date.split('_').map(Number);
            const date = new Date(year, month - 1, day);
            const shortYear = date.getFullYear().toString().substr(-2); // Extract last two digits of the year
            const barDate = `${date.getDate()} ${months[date.getMonth()]} ${shortYear} ${days[date.getDay()]}`;
            return { ...obj, bar_date: barDate };
        });
    }
    const getDataForEngagement = useCallback(async () => {
        try {
            setLoading(true)
            // ?brand_id=${user.brand_id}&start_date=${format(selectedDate.startDate, 'yyyy-MM-dd')}&end_date=${format(selectedDate.endDate, 'yyyy-MM-dd')}
            const response = await axios.get(`https://goswirl.world:3001/getAccumulatedAnalyticsDataByBrandIdAndDateForDashboard?brand_id=${user.brand_id}&start_date=${format(selectedDate.startDate, 'yyyy-MM-dd')}&end_date=${format(selectedDate.endDate, 'yyyy-MM-dd')}`);
            if (response.data.resp) {
                setAllAnalyticsData(response.data.resp)

                let totalData = calculateTotals(response.data.resp); // Assuming calculateTotals is defined somewhere
                const repeatViews = totalData.total_views - totalData.unique_views
                console.log(totalData);
                const engagement_rate =
                    ((parseInt(totalData.addtoCart) +
                        parseInt(totalData.buyNow) +
                        parseInt(totalData.askQuestion) +
                        parseInt(repeatViews)) /
                        parseInt(totalData.total_views)) *
                        100
                        ? (
                            ((parseInt(totalData.addtoCart) +
                                parseInt(totalData.buyNow) +
                                parseInt(totalData.askQuestion) +
                                parseInt(repeatViews)) /
                                parseInt(totalData.total_views)) *
                            100
                        ).toFixed(2)
                        : 0
                let x = (totalData.buyNow + totalData.addtoCart) / totalData.unique_views
                const ctr_rate = (x * 100).toFixed(2)

                totalData = { ...totalData, engagement_rate: isNaN(engagement_rate) ? 0 : engagement_rate, ctr_rate: isNaN(ctr_rate) ? 0 : ctr_rate }

                setOverallData(totalData);
            }
            const newData = addBarDate(response.data.resp); // Assuming addBarDate is defined somewhere
            setData(newData);
            setLoading(false)
        } catch (error) {
            console.log(error);
            setLoading(false)
        }
    }, [user, selectedDate]);
    useLayoutEffect(() => {
        getDataForEngagement()
    }, [selectedDate, getDataForEngagement])

    const CustomXAxisTick = ({ x, y, payload }) => {
        // Format the date string to display only the part you want
        const formattedDate = payload.value.slice(0, 9); // Assuming you want to display from the month onwards

        return (
            <text style={{ fontSize: "10px" }} x={x} y={y} dy={16} textAnchor="middle" fill="#666">
                {formattedDate}
            </text>
        );
    };
    const titleToolVal = () => {
        if (activeTab === "tab1") {
            return "Engagement Rate"
        } else if (activeTab === "tab2") {
            return "CTR Rate"
        } else if (activeTab === "tab3") {
            return "Add To Cart"
        } else if (activeTab === "tab4") {
            return "Buy Now"
        } else if (activeTab === "tab5") {
            return "Ask Questions"
        } else {
            return "Value"
        }
    }
    const CustomTooltip = ({ active, payload, label }) => {
        if (active && payload && payload.length) {
            return (
                <div className="custom-tooltip bg-white p-2" style={{ borderRight: "2px solid #77BBFF", borderLeft: "2px solid #77BBFF", borderRadius: "5px" }}>
                    <span className="label">Date : {`${label}`}</span> <br />
                    {/* {activeTab !== "tab3" && <span>Value : {payload[0].value}</span> } */}
                    {/* {activeTab === "tab3" ? <span>Watch Time : {convertSecondsToTime(payload[0].value)}</span> : <span>Value : {payload[0].value}</span>} */}
                    <span>{titleToolVal()} : {activeTab === "tab1" || activeTab === "tab2" ? `${payload[0].value} %` : payload[0].value}</span>
                </div>
            );
        }

        return null;
    };

    return (
        <>
            <div className="d-flex w-100 align-items-center">
                <ul className="nav nav-tabs">
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab1' ? 'active' : ''}`} onClick={() => handleTabClick('tab1')}>
                            <p>Engagement rate</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.engagement_rate ? parseFloat(overalldata?.engagement_rate) : 0} />%</h3>
                            {/* <h3>{overalldata?.engagement_rate} %</h3> */}

                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab2' ? 'active' : ''}`} onClick={() => handleTabClick('tab2')}>
                            <p>CTR</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.ctr_rate ? parseFloat(overalldata?.ctr_rate) : 0} />%</h3>
                            {/* <h3>{overalldata?.ctr_rate} %</h3> */}
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab3' ? 'active' : ''}`} onClick={() => handleTabClick('tab3')}>
                            <p>Add to cart</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.addtoCart ? parseFloat(overalldata?.addtoCart) : 0} /></h3>
                            {/* <h3>{overalldata?.addtoCart}</h3> */}
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab4' ? 'active' : ''}`} onClick={() => handleTabClick('tab4')}>
                            <p>Buy now</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.buyNow ? parseFloat(overalldata?.buyNow) : 0} /></h3>
                            {/* <h3>{overalldata?.buyNow}</h3> */}
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab5' ? 'active' : ''}`} onClick={() => handleTabClick('tab5')}>
                            <p>Ask question</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.askQuestion ? parseFloat(overalldata?.askQuestion) : 0} /></h3>
                            {/* <h3>{overalldata?.askQuestion}</h3> */}
                        </div>
                    </li>
                    <li className="nav-item d-none">
                        <div className={`nav-link ${activeTab === 'tab6' ? 'active' : ''}`} onClick={() => handleTabClick('tab6')}>
                            <p>Share</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.engagement_rate ? parseFloat(overalldata?.engagement_rate) : 0} />%</h3>
                            {/* <h3>{overalldata?.engagement_rate}</h3> */}
                        </div>
                    </li>
                    <li className="nav-item d-none">
                        <div className={`nav-link ${activeTab === 'tab7' ? 'active' : ''}`} onClick={() => handleTabClick('tab7')}>
                            <p>Mute</p>
                            <h3><ZeroToHeroCount targetNumber={overalldata?.askQuestion ? overalldata?.askQuestion : 0} />%</h3>
                            {/* <h3>{overalldata?.askQuestion}</h3> */}
                        </div>
                    </li>
                </ul>
                <div className='ms-auto'>
                    <Link to="/admin/svideos" ><button className='btn btn-sm btn-outline-dark' onClick={() => { setTab('Reports') }}>View all  <MdOutlineKeyboardArrowRight /></button></Link>
                </div>
            </div>
            {/* <div>
            .overlay {
    position: absolute;
    width: 100vh;
    height: 74vh;
    background-color: rgba(255, 255, 255, 0.8);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
} */}

            {/* {loading && <div style={{ position: "absolute", width: "95%", height: "40vh", display: "flex", zIndex: "1000", alignItems: 'center', justifyContent: "center", overflow: 'hidden', backgroundColor: "rgba(0,0,0,0.1)" }}>

                <TailSpin
                    visible={true}
                    height="60"
                    width="60"
                    color="rgba(0,0,0,0.5)"
                    ariaLabel="tail-spin-loading"
                    radius="1"
                    wrapperStyle={{}}
                    wrapperClass=""
                />
            </div>} */}
            <div>
                <ResponsiveContainer className="chart_engagement" width="100%" height="100%">
                    <BarChart
                        width={500}
                        height={300}
                        data={data}
                        margin={{
                            top: 20,
                            right: 0,
                            left: 0,
                            bottom: 5,
                        }}
                    >
                        <CartesianGrid strokeDasharray="1 1" />
                        <XAxis dataKey="bar_date" tick={<CustomXAxisTick />} />
                        <YAxis />
                        {/* <XAxis dataKey="name" /> */}
                        <Tooltip content={<CustomTooltip />} />
                        {/* <XAxis dataKey="name">
                        <Label value="Dates" offset={-5} position="insideBottom" />
                    </XAxis>
                    <YAxis>
                        <Label value="Views" offset={7} position="insideLeft" angle={-90} />
                    </YAxis> */}

                        {/* <Tooltip /> */}
                        {/* <Legend /> */}
                        {activeTab === "tab1" && <Bar dataKey="engagement_rate" maxBarSize={20} fill="#77BBFF" />}
                        {activeTab === "tab2" && <Bar dataKey="ctr_rate" maxBarSize={20} fill="#77BBFF" />}
                        {activeTab === "tab3" && <Bar dataKey="addtoCart" maxBarSize={20} fill="#77BBFF" />}
                        {activeTab === "tab4" && <Bar dataKey="buyNow" maxBarSize={20} fill="#77BBFF" />}
                        {activeTab === "tab5" && <Bar dataKey="askQuestion" maxBarSize={20} fill="#77BBFF" />}

                        {activeTab === "tab1" && <Legend payload={
                            [1].map(
                                (item, index) => ({
                                    id: "index",
                                    type: "rect",
                                    value: `Engagement Rate`,
                                    color: "#77BBFF"
                                })
                            )
                        } />}
                        {activeTab === "tab2" && <Legend payload={
                            [1].map(
                                (item, index) => ({
                                    id: "index",
                                    type: "rect",
                                    value: `CTR Rate`,
                                    color: "#77BBFF"
                                })
                            )
                        } />}
                        {activeTab === "tab3" && <Legend payload={
                            [1].map(
                                (item, index) => ({
                                    id: "index",
                                    type: "rect",
                                    value: `Add To Cart`,
                                    color: "#77BBFF"
                                })
                            )
                        } />}
                        {activeTab === "tab4" && <Legend payload={
                            [1].map(
                                (item, index) => ({
                                    id: "index",
                                    type: "rect",
                                    value: `Buy Now`,
                                    color: "#77BBFF"
                                })
                            )
                        } />}
                        {activeTab === "tab5" && <Legend payload={
                            [1].map(
                                (item, index) => ({
                                    id: "index",
                                    type: "rect",
                                    value: `Ask Questions`,
                                    color: "#77BBFF"
                                })
                            )
                        } />}

                    </BarChart>
                </ResponsiveContainer>

            </div>

        </>
    );
};

export default EngageMent;
