import React, { useState, useEffect, useCallback } from 'react';
import { BarChart, Bar, ResponsiveContainer, XAxis, YAxis, CartesianGrid, Legend, Tooltip } from 'recharts';
import "../chart.css"
import axios from 'axios';
import { numberFormatShort } from '../../utils/utils';
import { useSelector } from 'react-redux';
import { format } from 'date-fns';
import "../chart.css"
import { Link } from 'react-router-dom';
import { MdOutlineKeyboardArrowRight } from "react-icons/md";

const Perfomance = ({ selectedDate, setTab, allAnalyticsData }) => {
    const [data, setData] = useState([])
    const [overalldata, setOverallData] = useState({})
    const user = useSelector((state) => state.auth.user)

    const [activeTab, setActiveTab] = useState('tab1');

    const handleTabClick = (tab) => {
        setActiveTab(tab);
    };

    function convertSecondsToTime(seconds) {
        if (isNaN(seconds) || seconds < 0) {
            return "0";
        }

        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const remainingSeconds = seconds % 60;

        if (hours > 0) {
            return `${hours}hr ${minutes}min ${remainingSeconds}sec`;
        } else if (minutes > 0) {
            return `${minutes}min ${remainingSeconds}sec`;
        } else {
            return `${remainingSeconds}sec`;
        }
    }



    function calculateTotals(data) {
        return data.reduce((totals, entry) => {
            console.log(entry);
            totals.unique_views += entry.total_unique_views;
            totals.total_views += entry.total_views;
            totals.watch_time += entry.watch_time;

            return totals;
        }, { unique_views: 0, total_views: 0, watch_time: 0 });
    }
    function addBarDate(array) {
        const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

        return array.map(obj => {
            const [year, month, day] = obj.date.split('_').map(Number);
            const date = new Date(year, month - 1, day);
            const shortYear = date.getFullYear().toString().substr(-2); // Extract last two digits of the year
            const barDate = `${date.getDate()} ${months[date.getMonth()]} ${shortYear} ${days[date.getDay()]}`;
            return { ...obj, bar_date: barDate };
        });
    }


    const getDataForPerfomance = useCallback(async (allAnalyticsData) => {
        try {
            // const response = await axios.get(`https://goswirl.world:3001/getBrandVideoDataByDateAndBrandId?brandId=${user?.brand_id}&start_date=${format(selectedDate.startDate, 'yyyy-MM-dd')}&end_date=${format(selectedDate.endDate, 'yyyy-MM-dd')}`);

            // if (response.data.resp) {
            const totalData = calculateTotals(allAnalyticsData); // Assuming calculateTotals is defined somewhere
            console.log(allAnalyticsData);
            console.log(totalData);
            setOverallData(totalData);
            // }

            const newData = addBarDate(allAnalyticsData); // Assuming addBarDate is defined somewhere
            setData(newData);
        } catch (error) {
            console.log(error);
        }
    }, [user, selectedDate]);
    console.log(allAnalyticsData);
    console.log(overalldata);
    useEffect(() => {
        if (allAnalyticsData.length > 0) {
            getDataForPerfomance(allAnalyticsData)
        }

    }, [selectedDate, getDataForPerfomance, allAnalyticsData])
    const CustomXAxisTick = ({ x, y, payload }) => {
        // Format the date string to display only the part you want
        const formattedDate = payload.value.slice(0, 9); // Assuming you want to display from the month onwards

        return (
            <text style={{ fontSize: "10px" }} x={x} y={y} dy={16} textAnchor="middle" fill="#666">
                {formattedDate}
            </text>
        );
    };

    const titleToolVal = () => {
        if (activeTab === "tab1") {
            return "Views"
        } else if (activeTab === "tab2") {
            return "Unique Views"
        } else if (activeTab === "tab3") {
            return "Watch Time"
        }
    }

    const CustomTooltip = ({ active, payload, label }) => {
        if (active && payload && payload.length) {
            return (
                <div className="custom-tooltip bg-white p-2" style={{ borderRight: "2px solid #77BBFF", borderLeft: "2px solid #77BBFF", borderRadius: "5px" }}>
                    <span className="label">Date : {`${label}`}</span> <br />
                    {/* {activeTab !== "tab3" && <span>Value : {payload[0].value}</span> } */}
                    {/* {activeTab === "tab3" ? <span>Watch Time : {convertSecondsToTime(payload[0].value)}</span> : <span>Value : {payload[0].value}</span>} */}
                    <span>{titleToolVal()} : {activeTab === "tab3" ? convertSecondsToTime(payload[0].value) : payload[0].value}</span>
                </div>
            );
        }

        return null;
    };
    return (
        <>
            <div className="d-flex w-100 align-items-center">
                <ul className="nav nav-tabs">
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab1' ? 'active' : ''}`} onClick={() => handleTabClick('tab1')}>
                            <p>Total Views</p>
                            <h3>{numberFormatShort(overalldata?.total_views)}</h3>
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab2' ? 'active' : ''}`} onClick={() => handleTabClick('tab2')}>
                            <p>Total Unique Views</p>
                            <h3>{numberFormatShort(overalldata?.unique_views)}</h3>
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className={`nav-link ${activeTab === 'tab3' ? 'active' : ''}`} onClick={() => handleTabClick('tab3')}>
                            <p>Video Watch Time</p>
                            <h3>{convertSecondsToTime(overalldata?.watch_time)}</h3>
                        </div>
                    </li>
                    <li className="nav-item d-none">
                        <div className={`nav-link ${activeTab === 'tab4' ? 'active' : ''}`} onClick={() => handleTabClick('tab4')}>
                            <p>Drop-out rate</p>
                            <h3>10% </h3>
                        </div>
                    </li>

                </ul>
                <div className='ms-auto'>
                    <Link to="/admin/svideos"><button className='btn btn-sm btn-outline-dark' onClick={() => { setTab('Reports') }}> View all <MdOutlineKeyboardArrowRight /></button></Link>
                </div>
                {/* <div className="tab-content mt-3">
                    <div className={`tab-pane fade ${activeTab === 'tab1' ? 'show active' : ''}`} id="tab1" role="tabpanel">
                        <h5>Content of Tab 1</h5>
                    </div>
                    <div className={`tab-pane fade ${activeTab === 'tab2' ? 'show active' : ''}`} id="tab2" role="tabpanel">
                        <h5>Content of Tab 2</h5>
                    </div>
                    <div className={`tab-pane fade ${activeTab === 'tab3' ? 'show active' : ''}`} id="tab3" role="tabpanel">
                        <h5>Content of Tab 3</h5>
                    </div>
                </div> */}
            </div>
            <ResponsiveContainer className="chart_engagement" width="100%" height="100%">
                <BarChart
                    width={500}
                    height={300}
                    data={data}
                    margin={{
                        top: 20,
                        right: 0,
                        left: 0,
                        bottom: 5,
                    }}
                >
                    <CartesianGrid strokeDasharray="1 1" />
                    <XAxis dataKey="bar_date" tick={<CustomXAxisTick />} />
                    <YAxis />
                    {/* <XAxis dataKey="bar_date" tick={<CustomXAxisTick />} label={{ value: 'Month', position: 'insideLeft', offset: -10, style: { fontWeight: 'bold', fontSize: '12px' } }} />
                    <YAxis label={{ value: 'Sales', angle: -90, position: 'insideLeft', offset: 0, style: { fontWeight: 'bold', fontSize: '12px' } }} /> */}
                    <Tooltip content={<CustomTooltip />} />



                    {activeTab === "tab1" && <Bar dataKey="total_views" maxBarSize={20} fill="#77BBFF" />}
                    {activeTab === "tab2" && <Bar dataKey="total_unique_views" maxBarSize={20} fill="#77BBFF" />}
                    {activeTab === "tab3" && <Bar dataKey="watch_time" maxBarSize={20} fill="#77BBFF" />}

                    {activeTab === "tab1" && <Legend payload={
                        [1].map(
                            (item, index) => ({
                                id: "index",
                                type: "rect",
                                value: `Total Views`,
                                color: "#77BBFF"
                            })
                        )
                    } />}
                    {activeTab === "tab2" && <Legend payload={
                        [1].map(
                            (item, index) => ({
                                id: "index",
                                type: "rect",
                                value: `Unique Views`,
                                color: "#77BBFF"
                            })
                        )
                    } />}
                    {activeTab === "tab3" && <Legend payload={
                        [1].map(
                            (item, index) => ({
                                id: "index",
                                type: "rect",
                                value: `Watch Time`,
                                color: "#77BBFF"
                            })
                        )
                    } />
                    }


                    {/* <Bar dataKey="uv" fill="#82ca9d" activeBar={<Rectangle fill="gold" stroke="purple" />} /> */}
                </BarChart>
            </ResponsiveContainer>
        </>
    );
};

export default Perfomance;
